import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

export default {
  components: {
    Swiper,
    SwiperSlide,
    contactBtn: require('~/components/common/contact-btn.vue').default,
  },
  props: {
    title: { type: String, default: null },
    portfolioList: { type: Array, default: () => [] },
    link: { type: String, default: null },
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        autoplay: { disableOnInteraction: false },
        breakpoints: {
          1024: { slidesPerView: 3, spaceBetween: 30 },
          800: { slidesPerView: 2, spaceBetween: 30 },
          340: { slidesPerView: 1, spaceBetween: 0 },
        },
        pagination: { el: '.swiper-pagination', clickable: true },
      },
    }
  },
  computed: {
    swiper() { return this.$refs.mySwiper.$swiper },
  },
  mounted() {
    if (!!window.IntersectionObserver) {
      let observer = new IntersectionObserver((entries, observer) => {
        entries.forEach((entry) => {
          this.swiper.autoplay.stop()
          if (entry.isIntersecting) {
            this.swiper.autoplay.start()
            observer.unobserve(entry.target)
          }
        })
      }, { rootMargin: "0px 0px -200px 0px" })

      observer.observe(document.querySelector(".portfolio"))
    }
  },
  methods: {},
}
